import { useState, useEffect  } from 'react';
import { withSize } from 'react-sizeme';
import Globe from 'react-globe.gl';
import globeImage from './earth-blue-marble.jpg';
import backgroundImage from './night-sky.png';
import bumpImage from './earth-topology.png';
import data from './minimal_countries.geojson';
import './World.css'

const World = ({ size }) => {
  const [countries, setCountries] = useState({ features: [] });
  const [hover, setHover] = useState();

  useEffect(() => {
    // load data
    fetch(data).then(res => res.json()).then(setCountries);
  }, []);

  return <Globe
    width={size.width}
    height={600}
    globeImageUrl={globeImage}
    backgroundImageUrl={backgroundImage}
    bumpImageUrl={bumpImage}
    lineHoverPrecision={0}

    polygonsData={countries.features.filter(d => d.properties.ISO_A2 !== 'AQ')}
    polygonAltitude={d => d === hover ? 0.05 : 0.01}
    polygonCapColor={d => d === hover ? 'rgba(79, 144, 204, 0.8)' : 'rgba(255, 255, 255, 0.05)'}
    polygonSideColor={() => 'rgba(0, 100, 0, 0.15)'}
    polygonStrokeColor={() => 'rgba(255, 255, 255, 0.1)'}
    polygonLabel={({ properties: d }) => `
        <b>${d.NAME_KR} (${d.ISO_A2})</b> <br>
        인구수: <i>${d.POP_EST.toLocaleString(navigator.language)}명 (${d.POP_YEAR})</i> <br>
        인구 순위: <i>${d.POP_RANK}</i> <br>
        1인당 GDP(nominal): <i>${d.GDP_NOMINAL_EST.toLocaleString(navigator.language)} (${d.GDP_NOMINAL_YEAR})</i> <br>
        1인당 GDP(PPP): <i>${d.GDP_PPP_EST.toLocaleString(navigator.language)} (${d.GDP_PPP_YEAR})</i> <br>
      `}
    onPolygonHover={setHover}
    polygonsTransitionDuration={300}
  />;
};

export default withSize()(World);
